.navbar {
    display: block;
    position: fixed;
    z-index: 10;
    background: rgba(#fff, 0.8);
    border-bottom: 1px solid #efefef;
    width: 100%;
    padding: 10px 0;
    -ms-flex-align: center;
        align-items: center;
}
.nav-target {
    position: absolute;
    top: -76px;
}
.navbar__container {
    max-width: 960px;
    padding: 0 10px;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.navbar__logo {
    background-image: url('/img/icon/katrin-logo.svg');
    background-size: contain;
    background-repeat: no-repeat;
    width: 350px;
    height: 50px;
}

.navbar-nav__item a {
    text-decoration: none;
    color: $ color-red;

    & . button {
          color: white;
      }
}

.navbar-nav {
    margin: 0;
    padding: 0;
    list-style: none;
}
.navbar-nav__item {
    display: inline-block;
    vertical-align: middle;
    padding-left: 25px;
    line-height: 35px;
    text-transform: uppercase;
  a {
    font-size: 15px;
    font-weight: normal;
  }
}
.navbar__burger {
    display: none;
}
@media screen and (max-width:639px) {
    .navbar__burger {
        display: block;
        border: 1px solid green;
        border-radius: 5px;
        height: 30px;
        width: 40px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        padding: 6px 6px;
        background: none;
    }

    .bar {
        position: relative;
        height: 2px;
        margin-bottom: 3px;
        background: green;
        -webkit-transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
        transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
        -o-transition: opacity 0.5s ease, transform 0.5s ease;
        transition: opacity 0.5s ease, transform 0.5s ease;
        transition: opacity 0.5s ease, transform 0.5s ease, -webkit-transform 0.5s ease;
    }

    nav {
        position: absolute;
        left: 100%;
        top: 0;

    li {
        width: 250px;
        padding-left: 0 !important;
        padding: 0;

> a {
      padding: 15px 25px;
      display: block;
  }

+ li {
      -webkit-box-shadow: 0 -10px 0 -9px #eee;
              box-shadow: 0 -10px 0 -9px #eee;
  }
}

.button {
    margin: 20px auto;
    display: block;
    max-width: calc(100% - 100px);
}

}

.navbar, .navbar ~ * {
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.navbar.open, .navbar.open ~ * {
    -webkit-transform: translateX(-250px);
        -ms-transform: translateX(-250px);
            transform: translateX(-250px);
}

.navbar.open {

.navbar__burger {

.bar:nth-child(1), .bar:nth-child(3) {
    opacity: 0;
}

}

.bar:nth-child(2) {
    -webkit-transform: rotate(45deg) scale(0.7, 0.7);
        -ms-transform: rotate(45deg) scale(0.7, 0.7);
            transform: rotate(45deg) scale(0.7, 0.7);
}

.bar:nth-child(2)::before {
    content: ' ';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #ccc;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

}
}



